body {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

.hide {
  display: none;
}

.breed-select {
  width: 300px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.select-box {
  justify-content: center;
  align-items: center;
  display: flex;
}

.cat-info {
  width: 450px;
  margin: 0 auto;
}

.loader {
  width: 48px;
  height: 48px;
  perspective: 500px;
  z-index: 9999;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:before, .loader:after {
  content: "";
  width: 24px;
  height: 48px;
  transform-origin: 0 0;
  background: #ff3d00;
  border-radius: 0 24px 24px 0;
  animation: flip 2s linear infinite alternate;
  position: absolute;
  top: 0;
  right: 0;
}

.loader:after {
  transform-origin: 100% 0;
  border-radius: 24px 0 0 24px;
  animation-delay: 1s;
  left: 0;
}

@keyframes flip {
  0%, 10% {
    transform: rotateY(0deg);
  }

  90%, 100% {
    transform: rotateY(-180deg);
  }
}

